:root {
  // base

  --font-family: "Poppins", sans-serif;
  --content-width: 1250px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors

  --color-light: #EEE;
  --color-accent: #00ADB5;
  --color-gray: #393E46;
  --color-main: #222831;

}
