/* stylelint-disable */

$laptop: 1440.98px;
$tablet-above: 1199.98px;
$tablet: 991.98px;
$mobile: 767.98px;
$mobile-m: 575.98px;
$mobile-s: 480.98px;

@mixin desktop {
  @media (min-width: ($laptop)) {
    @content;
  }
}

@mixin laptop {
  @media (max-width: ($laptop)) {
    @content;
  }
}

@mixin tablet-above {
  @media (max-width: ($tablet-above)) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: ($tablet)) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: ($mobile)) {
    @content;
  }
}

@mixin mobile-m {
  @media (max-width: ($mobile-m)) {
    @content;
  }
}

@mixin mobile-s {
  @media (max-width: ($mobile-s)) {
    @content;
  }
}
