@use 'sass:math';
@use 'functions' as *;
@use './mixins/fluid' as *;
@use './mixins/disable-mob-hover' as *;

html {
  &.is-lock {
    overflow: hidden;
  }
}

body {
  font-family: var(--font-family);
  font-style: normal;
  line-height: normal;
  background-color: var(--color-main);
  color: var(--color-light);
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
  scroll-behavior: smooth;
}

.page__body {
  margin: 0;
  min-width: 390px;
  min-height: 100%;
}

img {
  object-fit: cover;
}

.site-container {
  //overflow: hidden; // If you use position: sticky on your site, remove this setting.
}

.is-hidden {
  display: none !important; /* stylelint-disable-line declaration-no-important */
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;

  &::-webkit-search-decoration,
  &::-webkit-search-cancel-button,
  &::-webkit-search-results-button,
  &::-webkit-search-results-decoration {
    display: none;
  }
}

._container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

a,
button,
label,
input,
textarea,
select,
svg * {
  transition-duration: var(--transition-duration);
}

a {
  color: inherit;

  &[class] {
    text-decoration: none;
  }
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  margin: -1px !important;
  border: 0 !important;
  padding: 0 !important;
  white-space: nowrap !important;
  clip-path: inset(100%) !important;
  clip: rect(0 0 0 0) !important;
  overflow: hidden !important;
}

.button {
  color: var(--color-light);
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.50);
  font-size: 18px;
  font-weight: 700;
  border-radius: 24px;
  transition: background-color 0.3s ease 0s, color 0.3s ease 0s;
}

.button-accent {
  background-color: var(--color-accent);
  @include hover {
    background-color: rgba(57, 62, 70, 0.75);
    color: var(--color-light);
    outline: 2px dashed var(--color-accent);
    outline-offset: 2px;
  }
}

.btn-transparent {
  background: rgba(57, 62, 70, 0.75);
  backdrop-filter: blur(2px);
  column-gap: 10px;
  @include hover {
    outline: 2px dashed var(--color-light);
    outline-offset: 2px;
    background-color: var(--color-accent);
    color: var(--color-light);
  }
}
