@use './mixins/fluid' as *;
@use 'functions' as *;

@use "vars" as *;
@use "mixins" as *;
@use "fonts" as *;
@use "settings" as *;


// Header start ========================================================================================================================================================

.header {
  padding-block: 15px;
  border-bottom: 1px solid rgba(238, 238, 238, 0.10);
  background-color: var(--color-main);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1000;
  width: 100%;
  margin: 0 auto;
  transition: box-shadow 0.3s ease 0s;
  &.scrolling {
    box-shadow: 0 0 7px rgba(238, 238, 238, 0.20);
  }

  // .header__inner

  &__inner {}

  // .header__logo

  &__logo {
    svg {
      path.logo-animating {
        transform-box: fill-box;
        transform-origin: 80% 50%;
      }
    }
    @include hover {
      svg {
        path.logo-animating {
          animation: animaitingLogo 3s linear 0s infinite forwards alternate;
        }
      }
    }
  }

  // .header__nav

  &__nav {}

  // .header__list

  &__list {
    column-gap: fluid(40, 20);
  }

  // .header__item

  &__item {}

  // .header__link

  &__link {
    font-size: 18px;
    transition: all 0.3s ease 0s;
    position: relative;
    &:hover {
      color: var(--color-accent);
      &:after {
        width: 100%;
        transform: scale(1);
      }
    }
    &:after {
      content: '';
      position: absolute;
      left: 0;
      bottom: -2px;
      width: 0%;
      height: 2px;
      background-color: var(--color-accent);
      transform-origin: 50% 50%;
      transform: scale(0);
      transition: all 0.3s ease 0s;
    }
  }
}

// Header end ========================================================================================================================================================

// Hero section start ========================================================================================================================================================

.hero {
  padding-block: fluid(100, 20);
  margin-top: 84px;

  // .hero__body

  &__body {
    padding-left: 85px;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      width: 79px;
      height: 261px;
      left: 0px;
      top: -20px;
      background: url('../img/hero-arrow.svg') left 30px/79px 261px no-repeat;
      @include tablet-above {
        background: url('../img/hero-arrow.svg') left 50px/79px 70% no-repeat;
      }
    }
    @include tablet {
      padding-left: 0;
      padding-right: 0;
      &:before {
        content: none;
      }
    }
  }

  // .hero__title

  &__title {
    font-size: 80px;
    color: var(--color-light);
    font-weight: 700;
    line-height: 1;
    text-transform: uppercase;
    span {
      font-size: 68px;
      @include tablet-above {
        font-size: 50px;
      }
    }
    @include tablet-above {
      font-size: 63px;
    }
  }

  // .hero__buttons

  &__buttons {
    margin-top: 40px;
    column-gap: 24px;
    flex-direction: column;
    row-gap: 15px;
    @media (min-width: 481px){
        flex-direction: row;
    }
  }

  // .hero__btn

  &__btn {
    padding: 10px 32px;
    @include mobile-s {
      width: 100%;
      justify-content: center;
    }
  }

  // .hero__image

  &__image {
    text-align: right;
    svg {
      //object-fit: cover;
    }
    @include tablet {
      text-align: center;
      svg {
        width: 500px;
        height: auto;
        margin: 0 auto;
      }
    }
    @include mobile-m {
      svg {
        width: 100%;
      }
    }
  }

  .text-animate {
    position: relative;
    max-width: max-content;
    h1 {
      max-width: 460px;
      text-shadow: 0px 0px 1px rgba(#eee, 0.20);
      color: transparent;
      background-image: linear-gradient(to bottom, #eee 50%, #00ADB5 50%);
      background-repeat: no-repeat;
      -webkit-background-clip: text;
      background-position: 0 0;
      animation: heroBgText 8s linear infinite;
      @include tablet-above {
        max-width: 360px;
      }
      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0px;
        width: 0px;
        height: 100%;
        border-right: 2px solid rgba(#00ADB5, 0.3);
        z-index: 1;
        animation: heroCursorText 8s linear infinite;
      }
    }
    @include tablet {
      margin: 0 auto;
      text-align: center;
    }
  }
}

// Keyframes

@keyframes heroBgText {
  0%, 10%, 100% {
    background-position: -29rem 0;
  }
  65%, 85% {
    background-position: 0 0;
  }
}

@include tablet-above {
  @keyframes heroBgText {
    0%, 10%, 100% {
      background-position: -22.5rem 0;
    }
    65%, 85% {
      background-position: 0 0;
    }
  }
}

@keyframes heroCursorText {
  0%, 10%, 100% {
    width: 0;
  }
  65%, 78%, 85% {
    width: 100%;
    opacity: 1;
  }
  75%, 81% {
    opacity: 0;
  }
}

@keyframes animaitingLogo {
  from {
    transform: rotateY(0deg);
  }
  50%{
    transform: rotateY(360deg);
  }
  to {
    transform: rotateY(0deg);
  }
}

// Hero section end ========================================================================================================================================================

// About section start ========================================================================================================================================================

.about {
  padding-block: fluid(100, 20) fluid(120, 100);
  @include tablet {
    .row > * {
      padding-inline: 0;
    }
  }

  // .about__body

  &__body {
    position: relative;
    @include tablet {
      text-align: center;
    }
  }

  // .about__title

  &__title {
    color: var(--color-light);
    font-size: 64px;
    font-weight: 700;
    span {
      color: var(--color-accent);
    }
    @include mobile-m {
      font-size: 46px;
    }
  }

  // .about__text

  &__text {
    p {
      color: rgba(238, 238, 238, 0.75);
      font-size: 18px;
      @include mobile-m {
        font-size: 14px;
      }
    }
  }

  // .about__smile

  &__smile {
    display: inline;
    width: 24px;
    height: 24px;
  }

  // .about__image

  &__image {
    text-align: right;
    svg {
      object-fit: cover;
    }
    @include laptop {
      svg {
        width: 500px;
        height: auto;
      }
    }
    @include tablet {
      text-align: center;
      svg {
        margin: 0 auto;
      }
    }
    @include mobile-m {
      svg {
        width: 100%;
      }
    }
  }

  .about-decorate-1,
  .about-decorate-2,
  .about-decorate-3 {
    position: absolute;
  }
  .about-decorate-1 {
    top: -100px;
    left: 0;
    @include tablet {
      top: -70px;
    }
  }
  .about-decorate-2 {
    left: 100%;
    top: 0;
    @include tablet {
      top: -90px;
      left: auto;
      right: 0;
    }
  }
  .about-decorate-3 {
    left: 30%;
    top: 60%;
    @include laptop {
      top: 80%;
    }
    @include tablet {
      display: none;
    }
  }
}

// About section end ========================================================================================================================================================

// Portfolio section start ========================================================================================================================================================

.portfolio {
  padding-block: fluid(100, 60);

  // .portfolio__body

  &__body {}

  // .portfolio__title

  &__title {
    color: var(--color-light);
    font-size: 64px;
    font-weight: 700;
    margin-bottom: fluid(40, 20);
    span {
      color: var(--color-accent);
    }
    @include tablet {
      text-align: center;
    }
    @include mobile-m {
      font-size: 46px;
    }
  }

  // .portfolio__slider

  &__slider {
    border-radius: 16px;
    border: 40px solid rgba(57, 62, 70, 0.50);
    border-bottom: 0;
    position: relative;
    @include tablet {
      border: 20px solid rgba(57, 62, 70, 0.50);
      border-bottom: 0;
    }
    @include mobile-m {
      border: 10px solid rgba(57, 62, 70, 0.50);
      border-bottom: 0;
    }
    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 73px;
      background-color: rgba(57, 62, 70, 0.50);
    }
  }

  // .portfolio__slide

  &__slide {
    position: relative;
  }

  // .portfolio__card

  &__card {
    &-overlay {
      img {
        filter: brightness(85%);
      }
    }
  }

  // .portfolio__wrapper

  &__wrapper {
    opacity: 0;
    padding-block: 15px;
  }

  // .portfolio__link

  &__link {
    padding: 8px 32px;
    margin-left: 4px;
    @include tablet {
      padding: 4px 32px;
    }

  }

  .swiper-slide-active,
  .swiper-slide-prev,
  .swiper-slide-next {
    .portfolio__wrapper {
      opacity: 1;
      //background-color: rgba(57, 62, 70, 0.50);

    }
  }
  .swiper-button-prev:after,
  .swiper-button-next:after {
    @include tablet {
      font-size: 30px;
    }
    @include mobile-m {
      font-size: 20px;
    }
  }

  .swiper-button-prev,
  .swiper-button-next {
    @include tablet {
      top: calc(var(--swiper-navigation-top-offset, 50%) - 30px);
    }
  }

  .swiper-button-next {
    @include mobile-m {
      right: var(--swiper-navigation-sides-offset, 0)
    }
  }

  .swiper-button-prev {
    @include mobile-m {
      left: var(--swiper-navigation-sides-offset, 0)
    }
  }

  .swiper-button-next,
  .swiper-button-prev {
    color: var(--color-accent);
  }

  .swiper-pagination {
    bottom: 15px;
    max-width: max-content;
    left: auto;
    right: 0px;
    display: flex;
    align-items: center;
    column-gap: 10px;
    padding: 8px 15px;
    color: var(--color-light);
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.50);
    font-size: 18px;
    font-weight: 700;
    border-radius: 24px;
    transition: background-color 0.3s ease 0s, color 0.3s ease 0s;
    background-color: var(--color-accent);
    @include tablet {
      padding: 4px 15px;
    }
  }
}

// Portfolio section end ========================================================================================================================================================


// Footer start ========================================================================================================================================================

.footer {
  padding-block: fluid(80, 40) fluid(30, 20);

  // .footer__nav

  &__nav {
    margin-bottom: fluid(40, 30);
  }

  // .footer__list

  &__list {
    column-gap: fluid(40, 20);
  }

  // .footer__item

  &__item {}

  // .footer__link

  &__link {
    font-size: 18px;
    transition: all 0.3s ease 0s;
    position: relative;
    @include hover {
      color: var(--color-accent);
    }
  }

  // .footer__copyright

  &__copyright {
    a {
      @include fluid-text(16, 14);

      color: rgba(238, 238, 238, 0.75);
      text-decoration: none;
      @include hover {
        text-decoration: underline;
      }
    }
  }
}


// Footer end ========================================================================================================================================================

